<template>
    <div class="login">
      <!-- 导航栏组件 -->
      <van-nav-bar title="登录" left-arrow @click-left="$router.back()"/>

      <!-- 主要内容 -->
      <div class="container">
        <div class="title">
          <h3>手机号登陆</h3>
          <p>未注册的手机号登录后将自动注册</p>
        </div>

      <!-- 表单 -->
       <div class="form">
        <div class="form_item">
          <input v-model="mobile" maxlength="11" class="inp" type="text" placeholder="请输入手机号"/>
        </div>
        <div class="form_item">
          <input v-model="picCode" maxlength="5" class="inp" type="text" placeholder="请输入图形验证码"/>
          <img v-if="picUrl" :src=picUrl alt="" @click="getPicCode">
        </div>
        <div class="form_item">
          <input v-model="msgCode" class="inp" type="text"  placeholder="请输入短信验证码"/>
          <button @click="getCode">{{second === totalSecond ? '获取验证码' :'(' + second + ')' + '秒后重试'}}</button>
        </div>
        <button class="login_btn" @click="login" >登录</button>
       </div>
    </div>
    </div>
</template>

<script>
import { CodeLogin, getPicCode, getSmsCode } from '@/api/login'
// import { Toast } from 'vant'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picCode: '',
      picKey: '',
      picUrl: '',
      totalSecond: 60,
      second: 60,
      timer: null,
      mobile: '',
      msgCode: ''
    }
  },
  async created () {
    this.getPicCode()
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    // 图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picKey = key
      this.picUrl = base64
    },
    // 校验合法
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 获取短信验证码
    async getCode () {
      if (!this.validFn()) return

      const { status } = await getSmsCode(this.picCode, this.picKey, this.mobile)
      if (status === 200) {
        this.$toast('短信验证码发送成功')
      } else {
        this.$toast('短信验证码发送失败')
        this.getPicCode()
        return
      }

      if (!this.timer && this.second === this.totalSecond) {
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
      }
    },
    // 登录
    async login () {
      if (!this.validFn()) return
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的短信验证码')
        return
      }
      const res = await CodeLogin(this.mobile, this.msgCode)
      // console.log(res)

      if (res.status === 200) {
        this.$store.commit('user/setUserInfo', res.data)
        this.$toast('登录成功')

        // 登录成功后，判断是否有返回地址
        const backUrl = this.$route.query.backUrl
        if (backUrl) {
          this.$router.replace(backUrl)
        } else {
          this.$router.push('/')
        }
      } else {
        this.$toast(res.msg)
      }
    }
  }
}
</script>

<style lang="less" scoped>

  .container{
    padding:49px 29px;

    .title{
      margin-bottom: 20px;
      h3{
        font-size: 26px;
        font-weight: 500;
      }
      p{
        font-size: 14px;
        line-height: 40px;
        font-weight: 500;
        color: #b8b8b8;
      }
    }

    .form_item{
      border-bottom: 1px solid #f3f1f2;
      padding: 8px;
      margin-bottom: 14px;
      display: flex;
      align-items: center;
      .inp{
        display: block;
        height: 32px;
        font-size: 14px;
        flex:1;
      }
      img{
        width: 94px;
        height: 31px;
      }
      button{
        height: 31px;
        font-size: 13px;
        color: #cea26a;
        background-color: transparent;
        padding-right: 9px;
      }
    }

    .login_btn{
      width: 100%;
      height: 42px;
      margin-top: 39px;
      background: linear-gradient(90deg,#ecb53c,#ff9211);
      border-radius: 21px;
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      display: flex;
      justify-content: center;
      align-items: center;
      letter-spacing: 2px;
      box-shadow: 0px 10px 20px 0px rgba(0,0,0,.1);
    }
  }
</style>
